.App {
  text-align: center;
}
.full-size {
  float: left;
  width: 100%;
  height: auto;
}
.component {
  clear: both;
}

h1 {
  margin: 0 0 30px;
}
h2 {
  margin: 0 0 20px;
}
h3, h4 {
  margin: 0 0 10px;
  padding: 0;
}
h4 {
  font-weight: normal;
  font-style: italic;
}

.bold {
  font-weight: bold;
}
